import {React} from 'react';
import 'animate.css';
import TrackVisibility from 'react-on-screen';
import contactImg from "../assets/img/contact-img.svg";
import { Container, Row, Col } from "react-bootstrap";

import { useForm, ValidationError } from '@formspree/react';

export const ContactForm = () => {

    const [status, setStatuss] = ''
    const [buttonText="Envoyer", setButtonText] = ''
    
    const [state, handleSubmit] = useForm("xeqyldrg");


    return (
        <section className="contact" id="connect">
            <Container>
                <Row className="align-items-center">
                    <Col size={12} md={6}>
                        <TrackVisibility>
                            {({ isVisible }) =>
                                <img className={isVisible ? "animate__animated animate__zoomIn" : ""} src={contactImg} alt="Contact Us" />
                            }
                        </TrackVisibility>
                    </Col>

                    <Col size={12} md={6}>
                        <TrackVisibility>
                            {({ isVisible }) =>
                                <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                                    <h2>Entrer en contact</h2>

                                    <form onSubmit={handleSubmit}>
                                        <Row>

                                            <Col size={12} sm={6} className="px-1">
                                                <input
                                                    id="name"
                                                    type="text"
                                                    name="Nom"
                                                    placeholder="Nom"
                                                />
                                                <ValidationError
                                                    prefix="Nom"
                                                    field="text"
                                                    errors={state.errors}
                                                />
                                            </Col>
                                            <Col size={12} sm={6} className="px-1">
                                                <input
                                                    id="prename"
                                                    type="text"
                                                    name="Prnom"
                                                    placeholder="Prnom"
                                                />
                                                <ValidationError
                                                    prefix="Prnom"
                                                    field="text"
                                                    errors={state.errors}
                                                />
                                            </Col>

                                            <Col size={12} sm={6} className="px-1">
                                                <label htmlFor="email">
                                                </label>
                                                <input
                                                    id="email"
                                                    type="email"
                                                    name="Email"
                                                    placeholder="Email"
                                                />
                                                <ValidationError
                                                    prefix="Email"
                                                    field="email"
                                                    errors={state.errors}
                                                />
                                            </Col>

                                            <Col size={12} sm={6} className="px-1">
                                                <input
                                                    id="tel"
                                                    type="tel"
                                                    name="Tlphone"
                                                    placeholder="Tlphone"
                                                />
                                                <ValidationError
                                                    prefix="Tel"
                                                    field="numeric"
                                                    errors={state.errors}
                                                />
                                            </Col>

                                            <Col size={12} className="px-1">
                                                <textarea
                                                    rows="12"
                                                    id="message"
                                                    type="text"
                                                    name="Message"
                                                    placeholder="Message"
                                                />
                                                <ValidationError
                                                    prefix="Message"
                                                    field="text"
                                                    errors={state.errors}
                                                />
                                                <button type="submit" disabled={state.submitting}>
                                                <span>{buttonText}</span>
                                                </button>
                                            </Col>

                                        </Row>
                                        {
                                                state.succeeded &&
                                            <Col size={12} className="px-1">
                                                <div>
                                                <p className={state.succeeded=== false ? "danger" : "success"}>{'Message envoy avec succs'}</p>
                                                </div>
                                            </Col>
                                            }
                                    </form>

                                </div>}
                        </TrackVisibility>
                    </Col>
                </Row>
            </Container>
        </section>


    );
}